.container{
  margin-left: 12.45rem;
  width: calc(100vw - 12.45rem);
  height: 100vh;
  
}


.image{
  text-align: center;
  object-position: center center;
  object-fit: none;
  height: inherit;

}

.content{
margin:auto;
 color: var(--lightBackground);
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 height: 100vh;
 width: 100%;
}

.content h2{
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 2rem;
  margin-bottom: 1rem;
}
.content h1{
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 3rem;
}
.content div{
  display: inline-block;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 31rem;
}

.content p{
  font-family: 'Space Mono', monospace ;
  font-size: 1rem;

}
.coder{
  font-family: 'Space Mono', monospace ;
  font-weight: 700;
  font-size: 1.5rem;
}
@media (max-width: 990px) {
  .container{
    width: 101vw ;

    margin-left: -1px;
  }
}
