.container{
  background: var(--lightBackground);
}
.header{
  display: block;
}

.skills{
  font-family: "Roboto", sans-serif;
  display: flex;
  width: 100%;
  height: calc(100vh - 169px);
  justify-content: center;
  align-items: center;
}
.skills > div{
  display: flex;
  gap: 2.5rem;

}

.skills li{
  margin-top: 20px;
  margin-left: 35px ;
  font-size: 1.25rem;
  position: relative;
}

.personal li::before,
.languages li::before{

  content: '';
  width: 25px;
  height: 5px;
  background-color: var(--decoration);
  position: absolute;
  left: -30px;
  top: 10px;
}

.skills h3{
  font-size: 2rem;
}

.skills span{
  color: var(--decoration);
}

.skills p {
  margin-top: 10px;
  margin-left: 3.5rem;
}

.html,
.css,
.javascript,
.react,
.figma{
  position: relative;
}
.html::before,
.css::before,
.javascript::before,
.react::before,
.figma::before{
  content: '';
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  background-size: fit;
  width: 30px;
  height: 35px;
  left: -38px;
  top: -5px;

}

.html::before{
  background-image: url('../../Assets/html.svg');
}
.css::before{
  background-image: url('../../Assets/css.svg');
}
.javascript::before{
  background-image: url('../../Assets/javascript.svg');
}
.react::before{
  background-image: url('../../Assets/react.svg');
}
.figma::before{
  background-image: url('../../Assets/figma.svg');
 height: 40px;
}

.darkTheme{
  background-color: var(--darkBackground2);
  color: var(--lightBackground2);
}


@media (max-width: 650px ) {
  .skills > div{
    margin-top: 100px;
    flex-direction: column ;
    gap: 2.5rem;
  
  }
  .skills h3{
    font-size: 1rem;
  }
  .skills li{
  margin-top: 10px;

    font-size: .75rem;
    
  }
  .skills p {
    font-size: .75rem;

  }
  .html::before,
  .css::before,
  .javascript::before,
  .react::before,
  .figma::before{
    background-image: none;
    width: 25px;
    height: 5px;
    background-color: var(--decoration);
    position: absolute;
    left: -30px;
    top: 5px;
  }
  .personal li::before,
.languages li::before{
  top: 5px;
}
}