
.header{
  display: grid;
  text-align: center;
  align-items: center; 
}
.header h1,
.header h2
{
  grid-area: 1/1;
}

.header h1{
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 9rem;
  line-height: 169px;
  color: var(--darkHeader);
  white-space: nowrap;
}
.header h2{
  justify-self: center;
  width: fit-content;
  position: relative;
  margin-top: 15px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-size: 3rem;
  line-height: 56px;
  color: var(--navBar);
  white-space: nowrap;
  z-index: 1;
}
.header h2::before{
  content: '';
  position: absolute;
  width: 40px;
  height: 30px;
  background: var(--decoration);
  border-radius: 4px;
  top: 25px;
  left: -20px;
  z-index: -1;
}
.darkTheme h1{
color: var(--headerDark1);
}
.darkTheme h2{
  color: var(--lightBackground2);
}

@media (max-width: 990px) {

.header h2{
font-size: 2rem;

}
.header h1{
  font-size: 6rem;
  
  }
}

@media (max-width: 650px) {

  .header h1{
      display: none;    
      
      }
}
