.header{
  display: block;
}



.content{
  position: absolute;
  top: 0;
  left: 0;
 padding: 0 40px;
 margin-top: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: fit-content;
}

.content > div{
  display: flex;
  width: 56.25rem;
  height: 18,75rem;
  padding: 1.5rem;
  margin-bottom: 8rem;
  background-color: var(--lightBackground);
  border-radius: 8.60rem;
}

.content p {
  margin-bottom: 15px;
  line-height: 25px;
  
}


hr {
  width: 98%;
}
.content img {
 width: 230px;
 height: 280;
 border-radius: 8.60rem;
 border: 3px solid var(--darkBackground);
}
.context {
  display: flex;
  padding: 0 20px ;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.darkTheme{
  background-color: var(--darkBackground);
}
.darkTheme .content > div{
  background-color: var(--headerDark1);
  color: var(--lightBackground2);
}

@media (max-width: 990px) {
  .content{
    margin-top: 100px;
  }
  #aboutMe{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 50px auto;
  }
  #aboutMe p{
    font-size: .75rem;
  }

  .content img {
  display: none;
  }

  .content > div{
  
    border-radius: 10px;
  }
}

@media (max-width: 460px) {

  .content{
    margin-top: 70px;
  }


}