.header{
  width: 100%;
  display: flex;
  justify-content: center;
}

.content{
display: flex;
height: calc(100vh - 169px);
justify-content: center;
align-items: center;
gap: 50px;
width: 100%;
}


.content div{
  margin-bottom: 150px;
width: 280px;
height: 230px;
border-radius: 20px;
border: 1px solid black;
}

.darkTheme{
  background-color: var(--darkBackground) ;
}

.darkTheme .content div{
  background-color: var(--lightBackground2);
}

@media (max-width: 650px) {
  .content div{
    margin-bottom: 0;
    width: 200px;
    height: 200px;
  }
  .content{
    flex-direction: column;
  margin-top: 30px;
    gap: 10px;
 
    }
    
    
}