.container{
  background: var(--lightBackground)
}


.header{
  width: 100%;
  display: flex;
  justify-content: center;
}
.content{
  display: flex;
  align-items: center;
  justify-content: center;
  height: fit-content;
  width: 100%;
}

.content form{

  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  width: 600px;
}

.content label{
  padding: 15px 0;
}

.content button{
  color: black;
}

.darkTheme{
  background-color: var(--darkBackground2);
  color: var(--lightBackground2);
}

.darkTheme .content  button{
    color: var(--lightBackground2);
  }

