.container{
  font-family: "Roboto", sans-serif;

  position: relative;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0;
  left: 0;
  background-color: var(--navBar);
  width: 100vw;
  height: 50px;
  z-index: 10;
}
.menu{
  position: relative;
  margin-right: 10px;
  display: inline-block;
  background-color: var(--navBar);
  cursor: pointer;
  box-shadow: 0 0 0 2px var(--decoration);
  border-radius: 5px;
  width: 40px;
  height: 40px;
}

.menu::before{
  content: '';
  position: absolute;
  background-color: var(--decoration);

  top: 45%;
  left: 2.5px;
  width: 35px;
  box-shadow: 0 10px 0  var(--decoration),  0 -10px 0  var(--decoration) ;
  border-radius: 5px;
  height: 5px;
}

.subMenu{
  position: absolute;
  left: calc( -100vw + 50px);
  top: 45px;
  background-color: var(--navBar);
  padding-top: 5px;
  width: 100vw;
  height: 200px;
}

.subMenu > *{
  margin-left: 10px;
  color: var(--decoration);

}