@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&family=Roboto:wght@400;500;700&family=Space+Mono:wght@400;700&display=swap);

:root{
  --lightBackground: #EAEAEA;
  --lightBackground2: #FDFDFD;
  --darkBackground: #161616;
  --darkBackground2: #414141;
  --darkHeader: #D9D9D9;
  --decoration: #4098AB;
  --decoration2: #BB7D7D;
  --navBar: #112741;
  --headerDark1: #646464
}
* {
  box-sizing: border-box;
}
html{ scroll-behavior: smooth;}
body {
  background-color: #414141;
  background-color: var(--darkBackground2);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0;
  margin: 0px;
  --type-first: Helvetica, Arial, sans-serif;
  --type-second: 'Spectral', Georgia;
  font-family: var(--type-first);
  overflow-y:scroll;

  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
}



body::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}

h1,
h2,
h3,
h4,
p {
  margin: 0px;
}

ul,
li {
  list-style: none;
  padding: 0px;
  margin: 0px;
}

img {
  display: block;
  max-width: 100%;
}

button,
input {
  display: block;
  font-size: 1rem;
  font-family: var(--type-first);
}

a {
  text-decoration: none;
}
.container{
  box-sizing: border-box;
  position: relative;
  margin-left: 12.5rem;
  width: calc(100vw - 12.5rem);
  height: 100vh;
  padding: 20px;
  
}

@media (max-width: 990px) {
  .container{
    width: 100vw;
    margin-left: 0;
  }
}
