.container{
  width: 12.5rem;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--navBar);
  overflow-y:scroll;

}

.container {
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
}
.container::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}

.settings{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1.55rem;
}
.theme{
  color: var(--lightBackground2);
  cursor: pointer;
}

.theme:hover{
  color: yellow;
}

.button{
  color: var(--lightBackground2);
  padding: 10px;
  cursor: pointer;
}

.language :global(p.active){
  color: var(--decoration);

}

.button:hover,
.button:active {
  color: var(--decoration);
}

.navBar{
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
  margin-top: 4rem;
  
}
.navBar hr{
  display: block;
  width: 1.6rem;
  border: 1px solid #748391;
}

.link{
  font-size: .75rem;
  font-family: "Roboto", sans-serif;
  color: var(--lightBackground);
}
.active{
  color: var(--decoration2) ;
}
.link:hover{
  color: var(--decoration2) ;
}

.navBar :global(a.active) {
  color: var(--decoration2) ;
}

.profile{
  font-family: "Playfair Display", serif;
  font-size: 1rem;
  color: var(--lightBackground);
  margin-top: 5rem;
  font-size: 1rem;
  display: block;
  text-align: center;
}

.profile span{
  color:var(--decoration2) ;

}

.profile::before{
  content: "";
  display: block;
  width: 7.5rem;
  height: 7.5rem;
  background: url(../Assets/profilePik.jpg)no-repeat center center;
  background-size: cover;
  border-radius: 50%;
  border: .25rem solid #646464;
  margin:  0 auto ;
  margin-bottom: 10px;
}


.icon{
  background-color: white;
}

.language{
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  color: white;
  display: flex;
  /* gap: 10px; */
  align-items: center;
  justify-content: center;
  width: 90px;
  height: 30px;
  border-radius: 20px;
  border: 1px solid var(--decoration);

}
.language a{
  color: white;
}
.language span{
  color:#748391;
}

.socialMedia{
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  margin: 4.4rem 0 1.55rem 0;
  gap: 1.55rem;
  
}
